import React, { useState, useEffect } from "react";
import Box from "@mui/material/Box";
import TextField from "@mui/material/TextField";
import OverallCentered from "../../components/Typesetting_components/Overall_centered";
import { useSelector, useDispatch } from "react-redux";
import { GetUserName } from "../../redux/action";
import CardMedia from "@mui/material/CardMedia";
import Typography from "@mui/material/Typography";
import { Button, CardActionArea, CardActions } from "@mui/material";
import Card from "@mui/material/Card";
import CardContent from "@mui/material/CardContent";
import Backdrop from "@mui/material/Backdrop";
import Modal from "@mui/material/Modal";
import Fade from "@mui/material/Fade";
import PropTypes from "prop-types";
import { styled } from "@mui/material/styles";
import Dialog from "@mui/material/Dialog";
import DialogTitle from "@mui/material/DialogTitle";
import DialogContent from "@mui/material/DialogContent";
import DialogActions from "@mui/material/DialogActions";
import IconButton from "@mui/material/IconButton";
import CloseIcon from "@mui/icons-material/Close";

export default function AddCompanion() {
  const [email, setEmail] = React.useState("");
  const [SearchButtonDisabled, setSearchButtonDisabled] = React.useState(false);
  //
  const [RelationshipStatus, setRelationshipStatus] = React.useState("null");
  //search
  const [IsOnClick, SetIsOnClick] = React.useState(false);
  async function search() {
    await fetch("api/check_if_there_is_a_binding?email=" + email, {
      method: "GET",
      headers: { "Content-Type": "application/json" },
    })
      .then((response) => {
        if (response.ok) {
          return response.text();
        } else {
          return Promise.reject("me_something went wrong!");
        }
      })
      .then((data) => {
        if (data === "true") {
          setRelationshipStatus("true");
        } else if (data === "false") {
          setRelationshipStatus("false");
        } else {
          setRelationshipStatus("null");
        }
        SetIsOnClick(true);
        // console.log("IsOnClick :" + IsOnClick);
        return console.log(data);
      })
      .catch((error) => {
        console.log(error);
      });
  }
  const Emailinput = () => {
    return (
      <>
        {/*  */}
        <TextField
          // id="outlined-name"
          label=" Email"
          autoFocus={true}
          error={false} //
          value={email}
          onChange={(event) => {
            setEmail(event.target.value);
          }}
          fullWidth={true}
          onBlur={() => {
            // alert(email);
          }}
        />
      </>
    );
  };
  const SearchButton = () => {
    return (
      <>
        <Button
          variant="outlined"
          disabled={SearchButtonDisabled}
          onClick={() => {
            search();
          }}
        >
          Search 
        </Button>
      </>
    );
  };
  const StateEmail = useSelector((state) => state.UserName);

  //
  useEffect(() => {
    SetIsOnClick(false);
    if (email.length === 0) {
      setSearchButtonDisabled(true);
    } else {
      if (email === StateEmail) {
        setSearchButtonDisabled(true);
      } else {
        setSearchButtonDisabled(false);
      }
    }
  }, [email]);

  const EmailUserState = ({ name, Contents }) => {
    return (
      <Card sx={{ width: "100%", mt: 3, mb: 3 }}>
        <CardActionArea>
          <CardMedia
            component="img"
            height="140"
            image="http://localhost:3000/img/wonderland%20(2).png"
            alt="green iguana"
          />
          <CardContent>
            <Typography gutterBottom variant="h5" component="div">
              {name}
            </Typography>
            <Typography variant="body2" color="text.secondary">
              {Contents}
            </Typography>
          </CardContent>
        </CardActionArea>
        <CardActions
          sx={{
            display: "flex",
            flexDirection: "row",
            justifyContent: "flex-end",
          }}
        >
          <ApplicationBindingButton />
          {/* <Button
            disabled={RelationshipStatus === "false" ? false : true}
            size="small"
            color="primary"
            onClick={() => {
              Application_binding();
            }}
          >
             TA 
          </Button> */}
        </CardActions>
      </Card>
    );
  };

  // 
  const Application_binding = async () => {
    await fetch(
      "api/application_binding?user_email=" +
        StateEmail +
        "&love_email=" +
        email,
      {
        method: "GET",
        headers: { "Content-Type": "application/Text" },
      }
    )
      .then((response) => {
        if (response.ok) {
          return response.text();
        } else {
          return Promise.reject("me_something went wrong!");
        }
      })
      .then((data) => {
        console.log(data);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const BootstrapDialog = styled(Dialog)(({ theme }) => ({
    "& .MuiDialogContent-root": {
      padding: theme.spacing(2),
    },
    "& .MuiDialogActions-root": {
      padding: theme.spacing(1),
    },
  }));

  function BootstrapDialogTitle(props) {
    const { children, onClose, ...other } = props;

    return (
      <DialogTitle sx={{ m: 0, p: 2 }} {...other}>
        {children}
        {onClose ? (
          <IconButton
            aria-label="close"
            onClick={onClose}
            sx={{
              position: "absolute",
              right: 8,
              top: 8,
              color: (theme) => theme.palette.grey[500],
            }}
          >
            <CloseIcon />
          </IconButton>
        ) : null}
      </DialogTitle>
    );
  }

  BootstrapDialogTitle.propTypes = {
    children: PropTypes.node,
    onClose: PropTypes.func.isRequired,
  };

  // 
  const ApplicationBindingButton = () => {
    const [open, setOpen] = React.useState(false);

    const handleClickOpen = () => {
      setOpen(true);
    };
    const handleClose = () => {
      setOpen(false);
    };

    return (
      <div>
        <Button
          disabled={RelationshipStatus === "true" ? true : false}
          variant="outlined"
          onClick={handleClickOpen}
        >
          Open dialog
        </Button>

        <BootstrapDialog
          onClose={handleClose}
          aria-labelledby="customized-dialog-title"
          open={open}
        >
          <BootstrapDialogTitle
            id="customized-dialog-title"
            onClose={handleClose}
          >
            
          </BootstrapDialogTitle>
          <DialogContent dividers>
            <Typography gutterBottom>
              
            </Typography>
            <Typography gutterBottom>
              
            </Typography>
            <Typography gutterBottom>
              
            </Typography>
            <Typography gutterBottom>
              DRDR
            </Typography>
            <Typography gutterBottom>
              DR
            </Typography>
            <Typography gutterBottom>
              
            </Typography>
            <Typography gutterBottom>{StateEmail}</Typography>
            <Typography gutterBottom>
              TA 
              {email}
            </Typography>
          </DialogContent>
          <hr />
          <DialogActions>
            <Button autoFocus onClick={handleClose}>
              
            </Button>
            <Button
              sx={{ color: "red" }}
              autoFocus
              onClick={() => {
                Application_binding();
                handleClose();
              }}
            >
              
            </Button>
          </DialogActions>
        </BootstrapDialog>
      </div>
    );
  };

  //
  const NotFound = () => <>email:{email}</>;
  //
  const UserSingle = () => (
    <>
      <EmailUserState name={email} Contents={""} />
    </>
  );

  //
  const AlreadyHaveAPartner = () => <> email:{email} </>;

  //
  const UserRelationshipsState = () => {
    if (RelationshipStatus === "true") {
      return <AlreadyHaveAPartner />;
    } else if (RelationshipStatus === "false") {
      return <UserSingle />;
    }
    return <NotFound />;
  };

  return (
    <>
      <OverallCentered>
        <>
          <Box sx={{ textAlign: "center", mb: 3 }}></Box>
          <Emailinput></Emailinput>
          {IsOnClick ? <UserRelationshipsState /> : null}
        </>
      </OverallCentered>
      <Box
        sx={{
          width: "100%",
          textAlign: "center",
          position: "fixed",
          bottom: 100,
        }}
      >
        <SearchButton sx={{ width: "100px" }}></SearchButton>
      </Box>
    </>
  );
}
